<template>
  <div class="nodeBox">
    <div class="labelName"
      :draggable="nodeObj.name=='ÂÆàÊä§ÈÅóËøπ'?false:true"
      @dragstart="dragstart($event,nodeObj,broNodeArr)"
      @dragover="dragover($event,nodeObj)"
      @drop="drop($event,nodeObj,broNodeArr)"
      @dragend="dragend($event,nodeObj,broNodeArr)"
    >
      {{ nodeObj.name }}
    </div>
    <div class="childBox" v-if="nodeObj.children.length !== 0">
      <node-box
        v-for="item in nodeObj.children"
        :key="item.index"
        :nodeObj="item"
        :broNodeArr="nodeObj.children"
      ></node-box>
    </div>
  </div>
</template>

<script>
import eventBus from "../bus"
export default {
  name: 'NodeBox',
  data() {
    return {
      dragNode:{}
    }
  },
  props: {
    nodeObj: Object,
    broNodeArr: Array
  },
  // computed:{

  // },
  methods:{
    dragstart(e,nodeObj,broNodeArr){
      // console.log('üêâdragÁßªÂä®ÁöÑÁÇπ‰Ωç',nodeObj.name,);
      eventBus.$emit("get-drag-node",nodeObj)//‰øùÂ≠òÊãñÊõ≥ÁÇπ‰Ωç‰ø°ÊÅØ
      let transData=JSON.stringify(nodeObj)
      e.dataTransfer.setData("node",transData)//Ë¶Å‰º†ÁöÑÊï∞ÊçÆ
      let index=broNodeArr.findIndex(item=>JSON.stringify(item)===transData)//ÂØªÊâæÊï∞ÁªÑ‰∏≠ÁöÑÁ¥¢Âºï
      eventBus.$emit("trans-drag-node-index", index)//‰øùÂ≠òindex
    },
    dragover(e){
      e.preventDefault()
    },
    drop(e,nodeObj){
      console.log('üêâdropÂà∞ÁöÑÁÇπ‰Ωç',nodeObj.name);
      let getData=JSON.parse( e.dataTransfer.getData("node"))//Ëé∑ÂèñdragÁöÑÊï∞ÊçÆ

      let fatherDragToSon =this.ifFatherDragToSon(eventBus.dragNode,nodeObj)//Âà§Êñ≠ÊòØÂê¶Áà∂Âà∞Â≠ê
      eventBus.$emit("state-father-drag-to-son",fatherDragToSon)//‰øùÂ≠òÁà∂Âà∞Â≠êÁöÑÂà§Êñ≠‰ø°ÊÅØÔºåÁªôdragend‰ΩøÁî®
      if(fatherDragToSon)return//Â¶ÇÊûúÊòØÁà∂Âà∞Â≠êÂàôÂèñÊ∂à

      let dragToSelf=eventBus.dragNode===nodeObj//Âà§Êñ≠ÊòØÂê¶Ëá™ÁßªÂä®
      eventBus.$emit("state-move-to-self", dragToSelf)//‰øùÂ≠òËá™ÁßªÂä®Áä∂ÊÄÅ‰ø°ÊÅØÔºåÁªôdragend‰ΩøÁî®
      if(dragToSelf)return//Ëá™ÁßªÂä®ÔºåÂèñÊ∂à

      nodeObj.children.push(getData)//Ê≠£Â∏∏Êìç‰ΩúÔºåÂú®Êï∞ÁªÑÊ∑ªÂä†‰ø°ÊÅØÔºåÁ™ÅÁÑ∂ÂèëÁé∞ÂèÇÊï∞‰πüÂèØ‰ª•Áî®eventBus.dragNode
    },
    dragend(e,nodeObj,broNodeArr){
      // ÈÄöËøáeventBusËé∑ÂèñÊãñÊõ≥Êï∞ÁªÑÁöÑindex'
      console.log(e.dataTransfer.dropEffect,'dragendÊãñÊîædropEffectÊìç‰ΩúÊïàÊûú');
      let dropEffect=e.dataTransfer.dropEffect
      if(dropEffect=="move"){
        if(eventBus.fatherToSonState){
        console.log('ÁßªÂä®Âà∞Â≠êËäÇÁÇπ‰∫Ü,Âï•‰πü‰∏çÂÅö');
        return
        }
        if(eventBus.moveToSelfState){
          console.log("ÁßªÂä®Âà∞Ëá™Ë∫´‰∫ÜÔºåÂï•‰πü‰∏çÂÅö");
          return
        }
        let index=eventBus.dragNodeIndex
        console.log(index,'üêâdragendÊãøÂà∞‰∫Üindex');
        broNodeArr.splice(index,1)
      }
      
    },
    ifFatherDragToSon(dragObj,dropObj){//Âà§Êñ≠ÊòØÂê¶Áà∂ËäÇÁÇπÁßªÂä®Âà∞‰∫ÜÂ≠êËäÇÁÇπ
      if (dragObj.children.length === 0) return false;
      let newArr = [];
      function getAllName(dragObj) {
        newArr.push(...dragObj.children);
        if (dragObj.children.length === 0) {
          return;
        } else {
          for (let i = 0; i < dragObj.children.length; i++) {
            getAllName(dragObj.children[i]);
          }
        }
      }
      getAllName(dragObj);
      if (newArr.includes(dropObj)) {
        return true;
      }
      return false;
    }
  }
}
</script>

<style lang="scss">
.nodeBox {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  position: relative;
  &::before,
  &::after {
    content: '';
    border-right: 1px solid #ddd;
    height: 10px;
    width: 50%;
    position: absolute;
    top: 0;
    left: 0;
  }
  &:not(:first-child):before {
    border-top: 1px solid #ddd;
  }
  &:not(:last-child):after {
    left: 50%;
    border-right: none;
    border-top: 1px solid #ddd;
  }
  .labelName {
    width: 100px;
    height: 30px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    border-radius: 4px;
    background-color: purple;
    margin: 10px 10px 0 10px;
    font-weight: 700;
    user-select: none;
    position: relative;
  }
  .childBox {
    display: flex;
    position: relative;
    padding: 10px;
    &::after {
      content: '';
      position: absolute;
      border-left: 1px solid #ddd;
      height: 10px;
      left: 50%;
      top: 0;
    }
  }
}
#app > .nodeBox:before,
#app > .nodeBox:after {
  content: none;
}
</style>
